<template>
    <img src="../../assets/icons/caret-down.png" width="16" height="16" alt="expanded" v-if="modelValue" @click="toggleValue" />
    <img src="../../assets/icons/caret-right.png" width="16" height="16" alt="collapsed" v-else @click="toggleValue"/>
</template>

<script>
export default {
    name: "CaretToggle",
    setup(props, context){
        return {
            toggleValue(){
                context.emit('update:modelValue', !props.modelValue);
            }
        }
    },
    emits: ['update:modelValue'],
    props: {
        modelValue: {
            type: Boolean,
            required: true
        }
    }
}
</script>

<style scoped>
    img {
        display: inline-block;
    }

    img:hover {
        cursor: pointer;
    }
</style>