<template>
    <div class="wrapper">
        <h3 class="header-title">Dashboard CLS Europe</h3>
        <h3 class="header-time">{{dateTimeDisplay('HH:MM')}}</h3>
        <h3 class="header-date">Datum: {{dateTimeDisplay('dd-LL')}}</h3>
    </div>
</template>

<script>
import {DateTime} from 'luxon';
import {ref} from 'vue';

export default {
    name: "Header",
    setup(){
        const now = ref(DateTime.local());
        
        return {
            dateTimeDisplay(format){
                return now.value.toFormat(format);
            }
        }
    }
}
</script>

<style scoped lang="scss">
    .wrapper {
        display: grid;
        
        //Take one full row
        grid-template-columns: repeat(12, 0.125fr);
        
        .header-title {
            text-align: center;
            
            grid-column-start: 5;
            grid-column-end: 8;
        }

        .header-time {
            grid-column-start: 9;
            grid-column-end: 10;
        }

        .header-date {
            grid-column-start: 11;
            grid-column-end: 13;
        }
    }
</style>