<template>
    <Box :title="title" class="vertical-scrolling-container">
        <table class="dashboard-table">
            <tbody>
                <agenda-item v-for="item in items"
                             :key="item"
                             :date="item.date"
                             :title="item.title"
                />
            </tbody>
        </table>
    </Box>
</template>

<script>
import {DateTime} from 'luxon';

import Box from "@/components/Box";
import AgendaItem from "./AgendaItem";

export default {
    name: "Agenda",
    components: {
        Box,
        AgendaItem
    },
    setup(){
        return {
            items: [{
                title: 'Cursus',
                date: DateTime.local().plus({ days: 1 }).toISO()
            }, {
                title: 'Cursus',
                date: DateTime.local().minus({ hours: 3, minutes: 5 }).toISO()
            }, {
                title: 'Cursus',
                date: DateTime.local().plus({ days: 1, hours: 3, minutes: 5 }).toISO()
            }]
        }
    },
    props: {
        title: {
            type: String,
            required: true
        }
    }
}
</script>
