<template>
    <tr>
        <td>Mail van: {{sender}}</td>
        <td class="email-time-to-open">{{toTimeToOpenDisplay(receivedUtc)}}</td>
    </tr>
</template>

<script>
import {DateTime} from 'luxon';


export default {
    name: "MailboxEmail",
    setup(){
        //Emails need to be opened within 24 hours of being received
        const toTimeToOpenDisplay = dt => {
            //Since our emails are from the past; our received values will always be negative
            //subtract them from 24/60 respectively to get the time left to open them
            let { hours, minutes } = DateTime.fromISO(dt)
                .toUTC()
                .diffNow(['hours', 'minutes'])
                .toObject();
            
            hours = 24 + hours;
            
            //Luxon always returns our minutes as a floating point number
            minutes = 60 + Math.round(minutes);
            
            //60 minutes = 1 hour, we can ignore them in that case
            //Our float is never an exact 60, thus we'll have to perform the 'reset' ourselves
            if(minutes === 60){
                minutes = 0;
            }
            
            //Luxon always rounds our hours upwards
            if(minutes > 0){
                hours--;
            }
            
            return `${hours <= 9 ? '0' : ''}${hours}:${minutes <= 9 ? '0' : ''}${minutes}`
        }
        
        return {
            toTimeToOpenDisplay
        }
    },
    props: {
        receivedUtc: {
            type: String,
            required: true
        },
        sender: {
            type: String,
            required: true
        }
    }
}
</script>

<style scoped>
    .email-time-to-open {
        text-align: right;
    }
</style>