<template>
    <Box :title="title" class="vertical-scrolling-container">
        <div>
            <caret-toggle v-model="recentConnectionsExpanded" />
            <span>Recente verbindingen</span>
            <table class="dashboard-table" v-if="recentConnectionsExpanded">
                <tbody>
                    <teamviewer-link v-for="(link, idx) in myPartners"
                                     :key="idx"
                                     :name="link.name"
                    />
                </tbody>
            </table>
        </div>
        
        <div>
            <caret-toggle v-model="myPartnersExpanded" />
            <span>Mijn partners</span>
            <table class="dashboard-table" v-if="myPartnersExpanded">
                <tbody>
                    <teamviewer-link v-for="(link, idx) in myPartners"
                        :key="idx"
                        :name="link.name"
                    />
                </tbody>
            </table>
        </div>
    </Box>
</template>

<script>
import {ref} from 'vue';

import Box from "@/components/Box";
import CaretToggle from "./CaretToggle";
import TeamviewerLink from "./TeamviewerLink";

export default {
    name: "Teamviewer",
    components: {
        Box,
        CaretToggle,
        TeamviewerLink
    },
    setup(){
        const recentConnectionsExpanded = ref(false);
        const myPartnersExpanded = ref(false);
        
        return {
            recentConnections: [{
                name: 'NL-NB-023 (deze computer)'
            }, {
                name: 'PC'
            }, {
                name: 'PC_Anke'
            }],
            myPartners: [{
                name: 'NL-NB-023 (deze computer)'
            }, {
                name: 'PC'
            }, {
                name: 'PC_Anke'
            }],
            
            recentConnectionsExpanded,
            myPartnersExpanded
        }
    },
    props: {
        title: {
            type: String,
            required: true
        }
    }
}
</script>

<style scoped>
    .dashboard-table {
        width: 95%;
        margin-left: 5%;
    }
</style>