<template>
    <tr>
        <td>{{relativeDay}} {{time}}</td>
        <td>{{title}}</td>
    </tr>
</template>

<script>
import {DateTime} from 'luxon';

export default {
    name: "AgendaItem",
    setup(props){
        const uppercaseFirstLetter = str => str[0].toUpperCase() + str.substr(1);
        
        const date = DateTime.fromISO(props.date)
            .toLocal();
        
        return {
            relativeDay: uppercaseFirstLetter(date.toRelativeCalendar()),
            time: date.toFormat('HH:mm')
            
            // displayDate: `${uppercaseFirstLetter(date.toRelativeCalendar())} ${date.toFormat('HH:mm')}`
        }
    },
    props: {
        title: {
            type: String,
            required: true
        },
        date: {
            type: String,
            required: true
        }
    }
}
</script>
