<template>
    <tr class="birthday-calendar-item-root">
        <td>{{name}}</td>
        <td>{{age}} jaar</td>
        <td class="next-birthday-cell">{{nextBirthDay}}</td>
    </tr>
</template>

<script>
import {DateTime} from 'luxon';

export default {
    name: "BirthdayCalendarItem",
    setup(props){
        const now = DateTime.local();
        let birthDay = DateTime.fromFormat(props.birthDate, 'yyyy-MM-dd'); //2020-01-09
        
        //Ensure we take the correct year if the given birthday has already happened in the current year
        let nextBirthdayYear = now.year;
        if(birthDay.month < now.month || birthDay.day < now.month){
            nextBirthdayYear++;
        }
        
        const nextBirthDay = DateTime.fromObject({
            year: nextBirthdayYear,
            month: birthDay.month,
            day: birthDay.day
        });
        
        return {
            nextBirthDay: nextBirthDay.toFormat('yyyy-MM-dd'),
            age: nextBirthDay.year - birthDay.year
        }
    },
    props: {
        name: {
            type: String,
            required: true
        },
        birthDate: {
            type: String,
            required: true
        }
    }
}
</script>

<style scoped>
    .next-birthday-cell{
        text-align: right;
    }
</style>